/* ==== EFEECTO DE LUZ DINÁMICA ===== */
.light-effect {
  position: fixed;
  width: 300px;
  height: 300px;
  background: radial-gradient(
    circle,
    rgba(77, 171, 247, 0.1) 0%,
    rgba(77, 171, 247, 0) 70%
  );
  border-radius: 50%;
  pointer-events: none;
  z-index: -1;
  opacity: 0;
  transition: opacity 0.3s ease;
  transform: translate(-50%, -50%);
}

body:hover .light-effect {
  opacity: 1;
}
